    getTotalByDay(usersTimesheetTable: any, day: number) {
        const dayStr = day < 10 ? '0' + day : day.toString();
        const dateKey = `${this.month}-${dayStr}`;
        let total = 0;

        for (const userKey in usersTimesheetTable) {
          const userDays = usersTimesheetTable[userKey];
          const tasks = userDays[dateKey];
          if (tasks && tasks.length) {
            total += tasks.reduce(
              (sum: number, t: any) => sum + (parseFloat(t.decimal_time) || 0),
              0
            );
          }
        }
        if(this.FilterKey == 'normal'){
            const currentDate = new Date(dateKey);
            const currentDay = currentDate.getDate();

            const holiday = this.holidays.find(element => {
                const holidayDate = new Date(element.date);
                return currentDay === holidayDate.getDate();
            });

            if (holiday) {
                return ( '<div class="cell table-cell-green" title="Holiday">8.5</div>');
            }

            let timesheet_total = 0;
            for (const element of this.leaves) {
                const leave_date = new Date(element.leave_date);
                if(this.users_id == element.user_id && currentDay === leave_date.getDate()){
                    if(element.leave_type_id == 2){
                        if(element.duration == 'half'){
                            return this.sanitizer.bypassSecurityTrustHtml( `<div class="cell table-cell-red" title="Ngh khng php (Half)">${Number(total).toFixed(1)}</div>`);
                        } else {
                            return this.sanitizer.bypassSecurityTrustHtml( '<div class="cell table-cell-red" title="Ngh khng php (Full)">OFF</div>');
                        }
                    } else {
                        if(element.duration == 'half'){
                            if(element.duration_type == 'first_half'){
                                timesheet_total = timesheet_total + 4;
                            } else {
                                timesheet_total = timesheet_total + 4.5;
                            }
                        } else {
                            return this.sanitizer.bypassSecurityTrustHtml( '<div class="cell table-cell-indigo" title="Ngh c php (Full)">8.5</div>');
                        }
                    }
                }
            }
            if(timesheet_total > 0){
                return this.sanitizer.bypassSecurityTrustHtml(  `<div class="cell table-cell-yellow" title="Ngh c php (Half)">${Number(total+timesheet_total).toFixed(1)}</div>`);
            } else {
                if (total > 0) {
                    return this.sanitizer.bypassSecurityTrustHtml(  `<div class="cell">${Number(total).toFixed(1)}</div>`);
                }
            }
            return '';
        } else {
            return this.sanitizer.bypassSecurityTrustHtml(  `<div class="cell">${Number(total).toFixed(1)}</div>`);
        }
    }

