    getWorkingDays(month, range: Date[]) {
        if (!range || range.length !== 2) {
            this.workingInMonth = 0;
            return;
        }

        const start = new Date(range[0]);
        const end = new Date(range[1]);

        let workingDays = 0;

        for (let d = new Date(start); d <= end; d.setDate(d.getDate() + 1)) {
            const day = d.getDay();   // 0 = CN, 6 = T7
            if (day !== 0 && day !== 6) {
                workingDays++;
            }
        }

        this.workingInMonth = workingDays;
    }
    exportTableDataArray(data) {
		let total = 0;
		let dataReturn = Object.keys(data).map(key => {
			let value = parseFloat(data[key]);
			if (isNaN(value)) value = 0;
			total += value;
			return {
				key: key,
				value: value
			};
		});
		return { total: total, data: dataReturn };
	}
	getTimesheetTotal(user: any): number {
        let timesheet_total = user.timesheet_total;
        this.holidays.forEach((element, index) => {
            timesheet_total = timesheet_total + 8.5;
        });
        this.leaves.forEach((element, index) => {
            if(user.id == element.user_id && element.leave_type_id == 1){
                if(element.duration == 'half'){
                    if(element.duration_type == 'first_half'){
                        timesheet_total = timesheet_total + 4;
                    } else {
                        timesheet_total = timesheet_total + 4.5;
                    }
                } else {
                    timesheet_total = timesheet_total + 8.5;
                }
            }
        });
        return timesheet_total;
    }
    getTimesheetByDay(user: any, j: any) {
        const currentDate = new Date(`${this.month}-${j + 1}`);
        const currentDay = currentDate.getDate();
        const holiday = this.holidays.find(element => {
            const holidayDate = new Date(element.date);
            return currentDay === holidayDate.getDate();
        });

        if (holiday) {
            return ( '<div class="cell table-cell-green" title="Holiday">8.5</div>');
        }

        let timesheet_total = 0;
        for (const element of this.leaves) {
            const leave_date = new Date(element.leave_date);
            if(user.id == element.user_id && currentDay === leave_date.getDate()){
                if(element.leave_type_id == 2){
                    if(element.duration == 'half'){
                        return this.sanitizer.bypassSecurityTrustHtml( `<div class="cell table-cell-red" title="Ngh khng php (Half)">${Number(user.timesheet[j].value).toFixed(1)}</div>`);
                    } else {
                        return this.sanitizer.bypassSecurityTrustHtml( '<div class="cell table-cell-red" title="Ngh khng php (Full)">OFF</div>');
                    }
                } else {
                    if(element.duration == 'half'){
                        if(element.duration_type == 'first_half'){
                            timesheet_total = timesheet_total + 4;
                        } else {
                            timesheet_total = timesheet_total + 4.5;
                        }
                    } else {
                        return this.sanitizer.bypassSecurityTrustHtml( '<div class="cell table-cell-indigo" title="Ngh c php (Full)">8.5</div>');
                    }
                }
            }
        }
        if(timesheet_total > 0){
            return this.sanitizer.bypassSecurityTrustHtml(  `<div class="cell table-cell-yellow" title="Ngh c php (Half)">${Number(user.timesheet[j].value+timesheet_total).toFixed(1)}</div>`);
        } else {
            if (user.timesheet[j] && user.timesheet[j].check) {
                return this.sanitizer.bypassSecurityTrustHtml(  `<div class="cell">${Number(user.timesheet[j].value).toFixed(1)}</div>`);
            }
        }
        return '';
    }

    compareValues(timesheet: any, working_hours: any): boolean {
        const isEqual = Number(timesheet.value) === Number(working_hours);

        const todayCheck = new Date(timesheet.key);
        const isSaturday = todayCheck.getDay() === 6;
        const weekOfMonth = this.getWeekOfMonth(todayCheck);
        // const isSecondOrFourthWeek = (weekOfMonth === 2 || weekOfMonth === 4);

        const newDate = new Date();
        const today = startOfDay(new Date(newDate.getFullYear(), newDate.getMonth(), 25));
        const currentWeekMonday = startOfWeek(today, { weekStartsOn: 1 });

        const isWeekdayAndFuture = todayCheck > currentWeekMonday && getDay(todayCheck) >= 1 && getDay(todayCheck) <= 5;

        return isEqual;
        // (isSaturday && isSecondOrFourthWeek) ||
        //  isWeekdayAndFuture;
    }
    getWeekOfMonth(date: Date): number {
        const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
        const firstDayOfWeek = firstDay.getDay();
        const dayOfMonth = date.getDate();

        return Math.ceil((dayOfMonth + firstDayOfWeek) / 7);
    }
