    getTotalDaysInMonth(date: Date): string[] {
        const start = startOfMonth(date);
        const end = endOfMonth(date);

        const daysInMonth = [];
        for (let d = start; d <= end; d.setDate(d.getDate() + 1)) {
          daysInMonth.push({date:format(new Date(d), 'YYYY-MM-DD'), stt:Number(format(new Date(d), 'd')), day:getDate(new Date(d))});
        }
        return daysInMonth;
    }

    onRangeChange(range: Date[] | any) {
        if (!range || !Array.isArray(range) || range.length !== 2) return;

        const [rawStart, rawEnd] = range;
        const startDate = (rawStart instanceof Date) ? rawStart : new Date(rawStart);
        const endDate = (rawEnd instanceof Date) ? rawEnd : new Date(rawEnd);
        // this.selectedRange = [ startOfDay(startDate), endOfDay(endDate) ];
        this.month = this.datePipe.transform(startDate, 'yyyy-MM');
        this.daysInMonth = this.getDaysBetween(this.selectedRange[0], this.selectedRange[1]);

        // this.getUsers();
    }
    getDaysBetween(start: Date, end: Date): any[] {
        const s = new Date(start.getFullYear(), start.getMonth(), start.getDate());
        const e = new Date(end.getFullYear(), end.getMonth(), end.getDate());
        const days = [];
        for (let d = new Date(s); d <= e; d.setDate(d.getDate() + 1)) {
          const cur = new Date(d);
          days.push({
            date: this.datePipe.transform(cur, 'yyyy-MM-dd'),
            stt: cur.getDay(),
            day: cur.getDate()
          });
        }
        return days;
    }

