    groupTimesheetByProject(user) {
        const grouped = {};
        if(this.FilterKey == 'normal'){
            user.timesheet.forEach(t => {
                const key = `${t.project_name}__${t.task_name}`;
                if (!grouped[key]) grouped[key] = {};
                if (!grouped[key][t.date]) grouped[key][t.date] = [];
                grouped[key][t.date].push(t);
            });
        } else {
            // OT
            user.timesheet_ot.forEach(t => {
            const key = `${t.project_name}__${t.task_name}`;
            if (!grouped[key]) grouped[key] = {};
            if (!grouped[key][t.date]) grouped[key][t.date] = [];
            grouped[key][t.date].push(t);
            });
        }

        return grouped;
    }

