import {
	AfterViewInit,
	Component,
	OnDestroy,
	OnInit,
	ViewChild
} from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DataTableDirective } from 'angular-datatables';
import { ExportAsService, ExportAsConfig } from 'ngx-export-as';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';

import { UserService } from '../../../../../core/services/user.service';
import { AuthenticationService } from '../../../../../core/services/authentication.service';

import { DatatablesResponse } from '../../../../../core/helpers/datatables-response.helper';
import { environment } from '../../../../../../environments/environment';
import { TeamService } from '../../../../../core/services/team.service';

import 'datatables.net';
import 'datatables.net-bs4';
import { isNumber } from 'lodash';

@Component({
	selector: 'app-project-reports',
	templateUrl: './project-reports.component.html',
	styleUrls: ['./project-reports.component.scss']
})

export class ProjectReportsComponent implements OnInit,AfterViewInit  {
	private apiUrl = environment.apiUrl;
	@ViewChild(DataTableDirective, {static: true})
	dtElement: DataTableDirective;
	dtTrigger: Subject<any> = new Subject();
	isPageLoaded = false;
	dtOptions: any = {};
	loginUser: any;
	projectCount: any;
	countStatus: any;
	statusfilterId: number;
	projects = [];
	leaders = [];
	leader = [];
	userLists = [];
	teamsProject = [];
	expandedProjectId: number | null = null;
	expandedRowIndex: number | null = null;
	exportAsConfig: ExportAsConfig = {
		type: 'pdf',
		elementIdOrContent: 'projects_table',
	};

	constructor(
		public translate: TranslateService,
		private http: HttpClient,
		private teamService: TeamService,
		private userService: UserService,
		private authenticationService: AuthenticationService,
		private exportAsService: ExportAsService
	) {
		this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
	}

	ngOnInit() {
		this.getUserkeyBy();
		this.getTeams();
		this.loadProjectDatatable();
	}

	getUserkeyBy() {
		this.userService.getUserkeyBy().subscribe(data => {
			this.userLists = data;
		});
	}
	togglePayments(index: number) {
		this.expandedRowIndex = this.expandedRowIndex === index ? null : index;
	}
	toggleExpand(projectId: number) {
		if (this.expandedProjectId === projectId) {
		  this.expandedProjectId = null; // ng nu click li
		} else {
		  this.expandedProjectId = projectId; // m project mi
		}
	}
	getTeams() {
		this.teamService.getAll()
			.subscribe(
				data => {
                    this.teamsProject = data;
					// this.teams = data;
				});
	}
	filterByStatus(statusID) {
		this.statusfilterId = statusID;
		this.rerender();
	}
	loadProjectDatatable() {
		let that = this;
		this.dtOptions = {
			pagingType: 'full_numbers',
			pageLength: that.loginUser.settings.tables_pagination_limit,
			serverSide: true,
			searching: true,
			processing: true,
			responsive: true,
			dom: '<"html5buttons"B>ltfrtip',
			order: [0],
			columns: [
				{
					'sortable': true,
					'width': "5%",
					'target': [0]
				},
				{
					'sortable': true,
					'target': [1]
				},
				{
					'sortable': true,
					'target': [2]
				},
				{
					'sortable': true,
					'target': [3]
				},
				{
					'sortable': false,
					'target': [4]
				},
				{
					'sortable': false,
					'target': [5]
				},
				{
					'sortable': false,
					'target': [6]
				},
				{
					'sortable': false,
					'target': [7]
				},{
					'sortable': false,
					'target': [8]
				},{
					'sortable': false,
					'target': [9]
				},{
					'sortable': false,
					'target': [10]
				},
				// {
				// 	'sortable': true,
				// 	'target': [8]
				// },
				// {
				// 	'sortable': true,
				// 	'target': [9]
				// },
				// {
				// 	'sortable': true,
				// 	'target': [10]
				// },
				// {
				// 	'sortable': true,
				// 	'target': [11]
				// }
			],
			buttons: [
			],
			language: {
				"sEmptyTable":  this.translate.instant('common.datatable.sEmptyTable'),
				"sInfo":           this.translate.instant('common.datatable.sInfo'),
				"sInfoEmpty":      this.translate.instant('common.datatable.sInfoEmpty'),
				"sSearch": "",
				"sInfoPostFix":    this.translate.instant('common.datatable.sInfoPostFix'),
				"sInfoThousands":  this.translate.instant('common.datatable.sInfoThousands'),
				"sLengthMenu":     this.translate.instant('common.datatable.sLengthMenu'),
				"sLoadingRecords": this.translate.instant('common.datatable.sLoadingRecords'),
				"sProcessing":     this.translate.instant('common.datatable.sProcessing'),
				"sZeroRecords":    this.translate.instant('common.datatable.sZeroRecords'),
				"sSearchPlaceholder": this.translate.instant('common.datatable.sSearchPlaceholder'),
				"oPaginate": {
					"sFirst":    this.translate.instant('common.datatable.oPaginate.sFirst'),
					"sLast":     this.translate.instant('common.datatable.oPaginate.sLast'),
					"sNext":     this.translate.instant('common.datatable.oPaginate.sNext'),
					"sPrevious": this.translate.instant('common.datatable.oPaginate.sPrevious')
				},
				"oAria": {
					"sSortAscending":  this.translate.instant('common.datatable.oAria.sSortAscending'),
					"sSortDescending": this.translate.instant('common.datatable.oAria.sSortDescending')
				}
			},
			ajax: (dataTablesParameters: any, callback) => {
				dataTablesParameters.statusfilterId = this.statusfilterId;
				dataTablesParameters.leader = this.leader;
				this.http
				.post<DatatablesResponse>(this.apiUrl + '/api/projects/project-report', dataTablesParameters , {})
				.subscribe(resp => {
						this.projects = resp.data;
						this.leaders = resp['leader'];
						this.countStatus = resp['statusCount'];
						this.projects.forEach(element => {
							element.type = (() => {
								try {
								  const parsed = JSON.parse(element.type);
								  if (Array.isArray(parsed)) {
									return parsed;
								  }
								  return [parsed];
								} catch (e) {
									if(isNumber(element.type)){
										return [element.type];
									} else {
										return [];
									}
								}
							  })();
							  element.hashtag = (() => {
								try {
								  const parsed = JSON.parse(element.hashtag);
								  if (Array.isArray(parsed)) {
									return parsed;
								  }
								  return [parsed];
								} catch (e) {
									if(isNumber(element.hashtag)){
										return [element.hashtag];
									} else {
										return [];
									}
								}
							  })();
							  if (typeof element.payments === 'string') {
								element.payments = JSON.parse(element.payments || '[]');
							  }
							  if (!Array.isArray(element.payments)) {
								element.payments = [];
							  }
							  let total = {total:0, total_invoice:0, total_payment:0, total_estimated:0};
							  element.payments.forEach(payment => {
									const price = Number(payment.price) || 0;
									total.total += price;
									if (payment.payment_date) {
									total.total_payment += price;
									} else if (payment.invoice_date) {
									total.total_invoice += price;
									}else {
									total.total_estimated += price;
									}
							  });
							  element.total_payment = total;
						});
						this.isPageLoaded = true

						callback({
							recordsTotal: resp.recordsTotal,
							recordsFiltered: resp.recordsFiltered,
							data: [],
						});
					});
			}

		};
	}
    exportFiles(){
        this.http.post(this.apiUrl + '/api/defect/export-projects', {}, { responseType: 'blob' })
          .subscribe((blob: Blob) => {
            const link = document.createElement('a');
            const url = window.URL.createObjectURL(blob);
            link.href = url;
            link.download = 'projects.xlsx';
            link.click();
            window.URL.revokeObjectURL(url);
          });
    }
	// exportFiles(type) {
	// 	this.exportAsConfig.type = type;
	// 	this.exportAsService.save(this.exportAsConfig, this.translate.instant('reports.headings.project_report')).subscribe(() => {});
	// }
	leaderChange(event: any) {
        this.leader = event;
    }
	ngOnDestroy(): void {
		this.dtTrigger.unsubscribe();
	}

	rerender(): void {
		this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
			dtInstance.destroy();
			setTimeout(() => {
				this.dtTrigger.next();

				if(this.projects.length > 0) {
					$('.tfoot_dt').addClass('d-none');
				} else {
					$('.tfoot_dt').removeClass('d-none');
				}
			});
		});
	}

	ngAfterViewInit(): void {
		this.dtTrigger.next();
		this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
			dtInstance.columns().every(function () {
				const that = this;
				$('input', this.footer()).on('keyup change', function () {
					if (that.search() !== this['value']) {
						that.search(this['value']).draw();
					}
				});
			});
		});
	}
}
